

.block-background{
    position: fixed;
    top: 0;
    left: 0;
    opacity: 0.05;
    width: 327px;
    height: 779px;
    background: #7f4c3e;
}

.line{
    position: fixed;
    height: 376px;
    left: 71px;
    top: 173px;
    z-index: 115;
    border-left: solid 2px #FFFFFF;
    mix-blend-mode: difference;
}
.left-navigation{
    position: fixed;
    left: 62px;
    top: 600px;
    z-index: 115;
    & .g{
        width: 21px;
        display: block;
        height: 40px;
        margin-bottom: 35px;
    }

    & .b{
        display: block;
        width: 21px;
        height: 40px;
        margin-bottom: 35px;
    }
    & .s{
        display: block;
        width: 21px;
        height: 40px;
    }
}
