    .info{
        transform: none;
        position: absolute;
        display: block;
        width: 35px;
        right: 10px;
        top: 10px;
        & .info-show {
            width: 100%;
            height: 100%;
            cursor: pointer;
            background: #ACACAC;
            border-radius: 50%;
            &:hover {
            background: #916C58;
            }
        }
        & .info-close {
            width: 100%;
            height: 100%;
            cursor: pointer;
            background: #ACACAC;
            border-radius: 50%;
            &:hover {
            background: #916C58;
            }
        }
    }
}

