
    .aboutUs-g {
        padding-top: 208px;
        background-color: #FFFFFF !important;
        overflow: hidden;
    }
    .title {
        padding: 0 7.81vw;
        margin-bottom: 60px;
    }
    .title > h1 {
        font-weight: 600;
        font-style: normal;
        font-family: Oswald;
        font-size: 52px;
        margin: 0;
        margin-left: 96px;
        margin-bottom: 30px;
        line-height: 54px;
        letter-spacing: 0.02em;
        font-feature-settings: 'pnum' on, 'lnum' on;
        color: #000000;
    }
    .title > p {
        font-weight: 400;
        font-style: normal;
        font-size: 14px;
        margin-bottom: 10px;
        font-family: Raleway;
        font-style: normal;
        font-weight: normal;
        line-height: 28px;
        letter-spacing: 0.05em;
        font-feature-settings: 'pnum' on, 'lnum' on;
        color: #000000;
    }
    .image-container-aboutUs{
        position: relative;
        margin-bottom: 60px;
        padding-left: 246px;

        .image{
            width: 100%;
            max-height: 570px;
            display: block;
        }

        .skew-image{
            position: absolute;
            bottom: -27px;
            right: 150px;
        }

        .text-image{
            position: absolute;
            left: -25px;
            bottom: 20px;
            width: 67.2vw;//67.3%
            height: 5.2vw;//83px
            background: #916C58;
            transform: skew(-25deg);
            text-align: right;
            font-family: 'Oswald';
            font-style: normal;
            font-weight: 500;
            font-size: 2.25vw;//36px
            letter-spacing: 0.02em;
            font-feature-settings: 'pnum' on, 'lnum' on;
            color: #FFFFFF;
            &>p{
               line-height: 5.2vw;//83px
               margin: 0;
               margin-right: 60px;
               transform: skew(25deg);
            }
        }
    }
    .gallery-slider {
        position: relative;
        padding: 0 7.81vw;
    }
    .teams-slider {
        position: relative;
        padding: 60px 7.81vw;
    }
    .aboutUs-g-description {
        overflow: visible;
        grid-template-columns: repeat(2, 1fr);
        grid-column-gap: 15px;
        display: grid;
        margin-bottom: 60px;
        padding: 0 6.25vw;
        & ul {
            list-style: none;
            & li {
                font-family: 'Raleway';
                font-style: normal;
                font-weight: normal;
                font-size: 14px;
                line-height: 28px;
                letter-spacing: 0.05em;
                font-feature-settings: 'pnum' on, 'lnum' on;
            }
            & li::before {
                content: "\2BC0";
                color: #916C58;
                font-weight: bold;
                font-size: 10px;
                display: inline-block;
                width: 1.5em;
                margin-left: -1em;
            }
        }
    }
    .our-team-aboutUs-container{
        background: rgba(145, 108, 88, 0.1);
        padding-top: 60px;
        .our-team-container_header{
            font-family: 'Oswald';
            height: 83px;
            color: #000000;
            font-weight: 600;
            font-size: 48px;
            display: flex;
            padding-left: 246px;
            align-items: baseline;
            justify-content: space-between;
            position: relative;
            padding-right: 150px;
            p {
                margin: 0;
            }
        }
        .all-our-trainers-link{
            margin: 0;
            font-weight: bold;
            font-size: 12px;
            line-height: 14px;
            text-align: right;
            letter-spacing: 0.05em;
            color: #000000;
            text-transform: uppercase;
        }

        .trainers-container .trainer-item:nth-of-type(even) {
            margin-top:25px;
        }
    }
    .certificate-title {
        // width: 80%;
        // margin-left: auto;
        // margin-right: auto;
        padding: 0 7.81vw;
        margin-bottom: 60px;
        padding-top: 60px;
        h1 {
            font-weight: 600;
            font-style: normal;
            font-family: Oswald;
            font-size: 52px;
            margin: 0;
            margin-bottom: 40px;;
            text-align: center;
            line-height: 54px;
            letter-spacing: 0.02em;
            font-feature-settings: 'pnum' on, 'lnum' on;
            color: #000000;
        }
        p {
            font-weight: 400;
            font-style: normal;
            font-size: 14px;
            margin: 0;
            font-family: Raleway;
            font-style: normal;
            font-weight: normal;
            line-height: 28px;
            letter-spacing: 0.05em;
            text-align: center;
            font-feature-settings: 'pnum' on, 'lnum' on;
            color: #000000;
        }
    }
    .certificate-description {
        // width: 80%;
        // margin-left: auto;
        // margin-right: auto;
        padding: 0 7.81vw;
        padding-bottom: 60px;
        padding-top: 60px;
        p {
            font-family: Raleway;
            font-style: normal;
            font-weight: normal;
            font-size: 14px;
            line-height: 28px;
            font-feature-settings: 'pnum' on, 'lnum' on;
            color: #000000;
        }
    }
    .footer {
        height: 234px;
    }
