
@import 'swiper/swiper.min.css';
@import 'swiper/components/navigation/navigation.min.css';

.swiper-container {
    width: 100%;
    height: 100%;
    position: unset;
    margin-bottom: 25px;
    overflow: unset;
    ::v-deep(.swiper-button-prev) {
        --swiper-navigation-size: 20px;
        --swiper-navigation-color: #000;
        opacity: 1;
        left: 20px !important;
    }
    ::v-deep(.swiper-button-next) {
        --swiper-navigation-size: 20px;
        --swiper-navigation-color: #000;
        opacity: 1;
        right: 20px !important;
    }
}





.swiper-slide {
    font-size: 18px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: auto;
    &:nth-child(2n){
        margin-top: 30px;
    }
    & ::v-deep(.trainer-item){
        transition: all .2s ease-in;
        position: relative;
        margin-bottom: 0;
        width: 100%;
        height: 100%;

        &.is-scaled{
            transform:scale(1.3);
            z-index: 101;
            margin-bottom: 0;
        }

        img{
            display:block;
            cursor: pointer;
            width:100%;
        }
    }

  }

.swiper-slide-active {
    & .trainer-item{
        transform-origin: left;
    }
}
.swiper-slide-prev {
    transition: all .2s ease-in;
    pointer-events: none;
    opacity: 0;
}

.swiper-slide-next + .swiper-slide + .swiper-slide + .swiper-slide{
    transition: all .2s ease-in;
    pointer-events: none;
    opacity: 0;
}
.swiper-slide-next + .swiper-slide + .swiper-slide .trainer-item {
    transform-origin: right;
}

.swiper-slide-next + .swiper-slide .trainer-item,
.swiper-slide-next .trainer-item{
    transform-origin: center;
}


.swiper-slide img {
    display: block;
    // width: 100%;
    height: auto;
    object-fit: cover;
  }

