
    .team-g {
        padding-top: 208px;
        background-color: #FFFFFF !important;
    }
    .title {
        padding: 0 7.81vw;
        margin-bottom: 60px;
    }
    .title > h1 {
        font-weight: 600;
        font-style: normal;
        font-family: 'Oswald';
        font-size: 52px;
        margin: 0;
        margin-left: 96px;
        margin-bottom: 30px;
        line-height: 54px;
        letter-spacing: 0.02em;
        font-feature-settings: 'pnum' on, 'lnum' on;
        color: #000000;
    }
    .title > p {
        font-weight: 400;
        font-style: normal;
        font-size: 14px;
        margin-bottom: 10px;
        font-family: 'Raleway';
        font-style: normal;
        font-weight: normal;
        line-height: 28px;
        letter-spacing: 0.05em;
        font-feature-settings: 'pnum' on, 'lnum' on;
        color: #000000;
    }
    .title > b {
        font-style: normal;
        font-size: 14px;
        font-family: 'Raleway';
        font-style: normal;
        font-weight: bold;
        line-height: 28px;
        letter-spacing: 0.05em;
        text-transform: uppercase;
        font-feature-settings: 'pnum' on, 'lnum' on;
        color: #000000;
    }
    .tickets {
        grid-template-columns: repeat(3, 321px);
        grid-column-gap: 64px;
        display: grid;
        justify-content: center;
        padding: 0 7.81vw;
        & p {
            text-transform: uppercase;
            margin: 0;
            margin-bottom: 20px;
            padding-left: 10px;
            font-family: Oswald;
            font-style: normal;
            font-weight: 600;
            font-size: 24px;
            line-height: 36px;
            letter-spacing: 0.02em;
            font-feature-settings: 'pnum' on, 'lnum' on;
            color: #916C58;
        }
        & img {
            max-width: 321px;
            width: 100%;
            height: auto;
            margin-bottom: 30px;
        }
        & .detail {
            font-family: Raleway;
            font-weight: bold;
            font-size: 12px;
            letter-spacing: 0.05em;
            text-transform: uppercase;
            text-decoration: none;
            color: #000;
            display: flex;
            align-items: center;
            margin-bottom: 60px;
            padding-right: 20px;
            float: right;
            cursor: pointer;
            &:active{
                color: #916C58;
            }

            &:after{
                content: '';
                width: 31px;
                height:16px;
                background: url("../../assets/homeView/arrow-right.svg") no-repeat;
                margin-left: 15px;
            }
        }
    }
    .ticket-title {
        font-family: Oswald;
        font-style: normal;
        font-weight: 600;
        font-size: 24px;
        line-height: 36px;
        text-align: center;
        letter-spacing: 0.02em;
        font-feature-settings: 'pnum' on, 'lnum' on;
        color: #916C58;
        margin: 0 auto;
        margin-bottom: 28px;
        width: 100%;
        & span {
            text-transform: uppercase;
        }
    }
    .ticket-discript {
        width: 100%;
        grid-template-columns: repeat(2, 1fr);
        grid-column-gap: 25px;
        display: flex;
        margin-bottom: 50px;
        & img {
            max-width: 321px;
            width: 100%;
            margin-left: 20px;
        }
        & .ticket-period {
            width: 100%;
            & p {
                margin: 0;
                line-height: 22px;
                font-family: Raleway;
                font-style: normal;
                font-weight: normal;
                font-size: 14px;
                letter-spacing: 0.05em;
                font-feature-settings: 'pnum' on, 'lnum' on;
                color: #000000;
            }
        }
        & .ticket-period-price{
            display: flex;
            grid-column-gap: 25px;
            align-items: center;
            margin-top: 20px;
            & p{
               font-family: Raleway;
                font-style: normal;
                font-weight: 800;
                font-size: 18px;
                line-height: 28px;
                letter-spacing: 0.05em;
                font-feature-settings: 'pnum' on, 'lnum' on;
                color: #916C58;
                margin: 0;
            }
            & button {
                width: 200px;
                height: 46px;
                & span {
                   font-family: Raleway;
                    font-style: normal;
                    font-weight: 600;
                    font-size: 14px;
                    line-height: 16px;
                    letter-spacing: 0.1em;
                    text-transform: uppercase;
                    color: #FFFFFF;
                }
            }
        }
    }
    .ticket-datail {
        list-style: none;
        & li {
            font-family: Raleway;
            font-style: normal;
            font-weight: normal;
            font-size: 14px;
            line-height: 28px;
            letter-spacing: 0.05em;
            font-feature-settings: 'pnum' on, 'lnum' on;
            color: #000000;
        }

        & li::before {
            content: "\2BC0";
            color: #916C58;
            font-weight: bold;
            font-size: 8px;
            display: inline-block;
            width: 1.5em;
            margin-left: -1em;
        }
    }
    .popup-tickets-enter-active,
    .popup-tickets-leave-active {
        transition: all .2s ease;
    }
    .popup-tickets-enter-from,
    .popup-tickets-leave-to {
        opacity: 0;
    }
    .footer {
        height: 234px;
    }
