
.view-main-b {
    overflow: hidden;
    background-color: #FFFFFF;
}




.top-image-container{
  position: relative;

  .top-image{
    width: 100%;

    //reset inline mode for image
    display: block;
  }

  .skew-image-1{
    position: absolute;
    bottom: -27px;
    left: 100px;
  }

  .water-text{
    position: absolute;
    font-family: Raleway;
    font-style: normal;
    font-weight: 900;
    font-size: 7.5vw;
    line-height: 8.802vw;
    text-align: right;
    bottom: -7.5vw;
    right: 7.8125vw;
    color: transparent;
    & .top-text {
        margin: 0;
        -webkit-text-stroke-color: #fff;
        -webkit-text-stroke-width: 0.5px;
    }
    & .down-text {
        margin: 0;
        -webkit-text-stroke-color: #916C58;
        -webkit-text-stroke-width: 1px;
    }
  }
  .vector1 {
    position: absolute;
    width: 66.085vw; //1268.85px;
    height: 7.515vw; //144.29px;
    right: -35.9375vw; //-690px;
    bottom: 26.594vw; //510.62px;
    background: linear-gradient(172.97deg, rgba(255, 255, 255, 0.25) 2.64%, rgba(255, 255, 255, 0) 48.7%);
    transform: rotate(-75.49deg);
  }
  .vector2 {
    position: absolute;
    width: 66.085vw; //1268.85px;
    height: 3.705vw; //71.145px;
    right: -28.828vw; //-553.5px;
    bottom: 23.365vw; //448.62px;
    background: linear-gradient(172.97deg, rgba(255, 255, 255, 0.25) 2.64%, rgba(255, 255, 255, 0) 48.7%);
    transform: rotate(-75.49deg);
  }
  .vector3 {
    position: absolute;
    width: 66.085vw; //1268.85px;
    height: 3.705vw; //71.145px;
    right: -24.01vw; //-461px;
    bottom: 19.198vw; //368.62px;
    background: linear-gradient(172.97deg, rgba(255, 255, 255, 0.25) 2.64%, rgba(255, 255, 255, 0) 48.7%);
    transform: rotate(-75.49deg);
  }
}

.about-us{
  background-color: #ffff;
  padding: 80px 150px 60px;
  display: flex;
  flex-direction: column;

  .title{
    font-family: Oswald;
    font-style: normal;
    font-weight: 600;
    font-size: 48px;
    line-height: 54px;
    letter-spacing: 0.02em;
    font-feature-settings: 'pnum' on, 'lnum' on;
    color: #000000;
    position: relative;
    margin-bottom: 30px;
    padding-left: 96px;
    padding-bottom: 19px;
    & .line-background {
        position: absolute;
        bottom: 0;
        right: -170px;
        width: 1545px; //74.8599vw;
        height: 83px;
        background: #916C58;
        opacity: 0.07;
        transform: skew(-18deg);
    }
  }

  .container-text{
    font-family: Raleway;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 28px;
    letter-spacing: 0.05em;
    font-feature-settings: 'pnum' on, 'lnum' on;
    color: #000000;
    & p {
        margin: 0;
    }
  }
  .about-us-warning {
      display: flex;
        font-family: Raleway;
        font-style: normal;
        font-weight: bold;
        font-size: 14px;
        line-height: 28px;
        letter-spacing: 0.05em;
        font-feature-settings: 'pnum' on, 'lnum' on;
        color: #000000;
        & p{
            margin: 0;
            margin-left: 22px;
        }
  }

}

.image-container-aboutUs{
    position: relative;
    margin-bottom: 60px;
    padding-left: 246px;
    background-color: #FFFFFF;

    .image{
        width: 100%;
        max-height: 570px;
        display: block;
    }

    .skew-image{
        position: absolute;
        bottom: -27px;
        right: 150px;
    }

    .text-image{
        position: absolute;
        left: -25px;
        bottom: 20px;
        width: 67.2vw;//67.3%
        height: 5.2vw;//83px
        background: #916C58;
        transform: skew(-25deg);
        text-align: right;
        font-family: 'Oswald';
        font-style: normal;
        font-weight: 500;
        font-size: 2.25vw;//36px
        letter-spacing: 0.02em;
        font-feature-settings: 'pnum' on, 'lnum' on;
        color: #FFFFFF;
        &>p{
            line-height: 5.2vw;//83px
            margin: 0;
            margin-right: 60px;
            transform: skew(25deg);
        }
    }
}

.gallery-slider {
    position: relative;
    padding: 0 7.81vw;
    margin-bottom: 60px;
    background-color: #FFFFFF;
}

.brand-makeup {
    background-color: #FFFFFF;
    & h1 {
        font-family: Oswald;
        font-style: normal;
        font-weight: 600;
        font-size: 48px;
        line-height: 54px;
        text-align: center;
        letter-spacing: 0.02em;
        font-feature-settings: 'pnum' on, 'lnum' on;
        color: #000000;
        margin: 60px 0 40px 0;
    }
    & p {
        font-family: Raleway;
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 28px;
        letter-spacing: 0.05em;
        font-feature-settings: 'pnum' on, 'lnum' on;
        color: #000000;
        padding: 0 7.81vw;
        margin-bottom: 20px;
    }
    .link-1{
        font-family: Raleway;
        font-weight: bold;
        font-size: 12px;
        letter-spacing: 0.05em;
        text-transform: uppercase;
        text-decoration: none;
        color: #000;
        display: flex;
        align-items: center;
        margin-bottom: 60px;
        padding: 0 9.61vw;
        float: right;

        &:active{
            color: #916C58;
        }

        &:after{
            content: '';
            width: 31px;
            height:16px;
            background: url("../../assets/homeView/arrow-right.svg") no-repeat;
            margin-left: 15px;
        }
    }
}

