
@import 'swiper/swiper.min.css';

.overlay-swiper{
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    z-index: 999;
    background-color: black;
    display: flex;
    justify-content: center;
    align-items: center;

.swiper-container {
    width: 80%;
    height: 70%;
}


.swiper-navigation-prev-1,
.swiper-navigation-next-1 {
    position: absolute;
    top: 50%;
    z-index: 10;
    cursor: pointer;
  }

  .swiper-navigation-prev-1 {
    left: 0x;
    height: 8px;
    width: 31px;
    background: url("../../assets/images/arrow-right-1.svg") no-repeat;
    transform: rotate(180deg);
  }
  .swiper-navigation-next-1{
    right: 0;
    height: 8px;
    width: 31px;
    background: url("../../assets/images/arrow-right-1.svg") no-repeat;
  }


.swiper-slide {
  display: flex;
  justify-content: center;
  align-items: center;
}

.swiper-slide img {
  // display: block;
  // width: 100%;
  height: 100%;
  // object-fit: cover;
}

  .close {
    background-image: url("../../assets/images/menu-close.svg");
    background-repeat: no-repeat;
    width: 26px;
    height: 26px;
    position: absolute;
    right: 60px;
    top: 60px;
    &:hover {
        cursor: pointer;
        filter: brightness(1);
    }
  }

}
