
.footer{
    padding: 30px 150px;
    color: #fff;
    background-color: #000;



.line-1{
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    align-items: end;
    justify-items: center;


  .logo {
    height: 43px;
    background-size: contain;
    background-repeat: no-repeat;
    justify-self: normal;

    &.g {
      background-image: url("../../assets/images/g-level.svg");
    }
    &.b {
      background-image: url("../../assets/images/b-level.svg");
    }
    &.s {
      background-image: url("../../assets/images/s-level.svg");
    }
  }

  .timetable{
    font-family: Raleway;
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    letter-spacing: 0.1em;
  }

  .address {
    font-weight: bold;
    font-size: 14px;
    line-height: 16px;
    text-align: center;
    letter-spacing: 0.1em;
    white-space: nowrap;
  }

  .social-networks{
    justify-self: end;
    display: grid;
    grid-template-columns: repeat(3, 16px);
    grid-column-gap: 35px;

    & > a{
      text-decoration: none;
      width: 16px;
      height:16px;

      &.youtube{
        background-image: url("../../assets/images/youtube.svg");
      }

      &.facebook{
        background-image: url("../../assets/images/facebook.svg");
      }

      &.instagram{
        background-image: url("../../assets/images/instagram.svg");
      }


    }

  }

  }

  .line-2{
    margin-top: 40px;
    display: flex;
    justify-content: center;
    align-items: center;

    .link-1{
      text-align: center;

      color: #FFFFFF;
      opacity: 0.55;
      text-decoration: none;
      font-family: Raleway;
      font-weight: 600;
      font-size: 14px;
      line-height: 16px;
      letter-spacing: 0.1em;
      text-transform: uppercase;
      transition:opacity .2s ease-in;

      margin-right:60px;

      &:last-of-type{
        margin-right: 0;
      }

      &:hover{
        opacity:1;
      }
    }
  }

hr{
  background: #FFFFFF;
  opacity: 0.15;
  height: 1px;
  margin: 30px 0;
}
  .line-3{
    display: grid;
    grid-template-columns: 1fr 1fr;



    font-family: Raleway;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 14px;

    letter-spacing: 0.1em;
    text-transform: uppercase;
    font-feature-settings: 'pnum' on, 'lnum' on;
    color: #FFFFFF;

    .text-1{
    }


    .design-text{
      display: flex;
      align-items: flex-end;
      justify-self: end;

      .design-logo{
        margin-left: 12px;
        height: 23px;
        width: 71px;
        background: url("../../assets/images/design-logo.svg") no-repeat;
      }
    }
  }
}
