
.view-area-g{
  background-color: #fff;
  padding: 208px 0 60px;

    .title{
        font-family: Oswald;
        font-weight: 600;
        font-size: 52px;
        line-height: 54px;
        letter-spacing: 0.02em;
        font-feature-settings: 'pnum' on, 'lnum' on;
        color: #000000;
        margin-bottom: 30px;
        margin-left: 216px;
    }
    .description{
        padding: 0 150px;
        font-family: Raleway;
        font-size: 14px;
        line-height: 28px;
        letter-spacing: 0.05em;
        font-feature-settings: 'pnum' on, 'lnum' on;
        color: #000000;
    }

    .big-image{
        width:88%;
        margin-left: auto;
        display: block;
        margin-top: 60px;
        margin-bottom: 60px;
    }

    .gallery-slider {
        position: relative;
        padding: 0 7.81vw;
    }

    .open-popup-container{
        width: max-content;
        margin: 0 auto;
        display: flex;
        flex-direction: column;
        align-items: center;

        .open-popup-title{
            font-family: Oswald;
            font-weight: 600;
            font-size: 64px;
            line-height: 64px;
            text-align: center;
            letter-spacing: 0.02em;
            font-feature-settings: 'pnum' on, 'lnum' on;
            color: #000000;
        }

        .open-popup-text{
            margin: 30px 0;
            font-family: Raleway;
            font-size: 14px;
            line-height: 28px;
            letter-spacing: 0.05em;
            font-feature-settings: 'pnum' on, 'lnum' on;
            color: #000000;
        }
    }
}

